*{
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;
    
}

html, 
body,
#root,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
   }

   .movies{
    display: flex;
    flex-direction: column;
    align-items: center;    
}

.movies .movie{
    width:90%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;    
    color: #b2b3bc;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.25)
    
}

.movie img{
    position: relative;
    top: -50px;
    max-width: 100px;
    margin-right: 20px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.25)
    
}

.movie .movie__title,
.movie .movie__year{
    margin: 0px;
    font-weight: 500;
    width: 60%;
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 20px;
    color: #2c2c2c;
}

.movie .movie__genres{
    list-style: none;
    padding: 0;
    display: flex;
    margin: 5px 0px;
    flex-wrap:wrap;
}

.movie__genres li{
    margin-right: 10px;
    font-size: 10px;
}

.movie__summary{
    width:60%
}

@media screen and (min-width: 1200px){
    .movies {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap; 
        padding: 30px;
        padding-top: 70px;
        flex-direction: row;
        align-items: flex-start;

    }

    .movies .movie{
        width:40%;
        display: flex;
        align-items:flex-start; 
        justify-content: space-between;
 


    }
}